@charset "UTF-8";

.footer {
    text-align: center;
    height: 7vh;
}

.footer h5 {
    font-size: 16px;
}

@media screen and (max-width: 768px) { 
    .footer {
        display: flex;
        justify-content: center;
        text-align: center;
        padding: 0px 40px;
    }

    .footer h5 {
        font-size: 13px;
    }
}